




















import Vue from 'vue';
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/zh-tw';

export default Vue.extend({
  components: {
    DatePicker,
  },
  data() {
    let date = new Date();

    return {
      form: {
        date,
      },
    };
  },
  computed: {
    route(): string {
      if (this.$route.name) return this.$route.name;
      return '';
    },
    imgSrc(): string {
      let monthStr = String(this.form.date.getMonth() + 1).padStart(2, '0');
      let dateStr = String(this.form.date.getDate()).padStart(2, '0');

      return `111${monthStr}${dateStr}.png`;
    },
  },
  watch: {
    '$route.name': function () {
      this.resetDate();
    },
  },
  mounted() {
    this.resetDate();
  },
  methods: {
    recalculate() {},
    resetDate() {
      if (this.$route.name === 'pic1') this.form.date = new Date(2022, 0, 1);
      else if (this.$route.name === 'pic2')
        this.form.date = new Date(2022, 1, 1);
      else if (this.$route.name === 'pic3')
        this.form.date = new Date(2022, 2, 1);
      else if (this.$route.name === 'pic4')
        this.form.date = new Date(2022, 3, 1);
      else if (this.$route.name === 'pic5')
        this.form.date = new Date(2022, 4, 1);
      else this.form.date = new Date(2022, 5, 1);
    },
    disabledDate(date: Date, currentValue: Date[]): boolean {
      let firstDay = new Date(this.form.date);
      firstDay.setDate(1);
      let secondDay = new Date(this.form.date);
      secondDay.setDate(16);
      currentValue.push(firstDay);
      currentValue.push(secondDay);
      return !(date.getDate() == 1 || date.getDate() == 16);
    },
  },
});
